.card {
    max-width: 320px;
    margin-right: 80px;
}

.card:last-of-type {
    margin-right: 0;
}


.name {
    margin: 0;
    margin-top: 30px;
    font-family: inherit;
    font-style: normal;
    font-weight: 400;
    font-size: 22px;
    line-height: 130%;
    letter-spacing: 0.055em;
    text-transform: uppercase;
}

.text {
    margin: 0;
    margin-top: 10px;
    font-family: inherit;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
}

@media screen and (max-width: 768px) {
    .card {
        max-width: 280px;
        text-align: center;
    }
    .image {
        width: 280px;
    }

}